import { VuexClassModuleFactory } from "./module-factory";
export function Module(options) {
    return moduleDecoratorFactory(options);
}
function moduleDecoratorFactory(moduleOptions) {
    return function (constructor) {
        var factory = new VuexClassModuleFactory(constructor, moduleOptions);
        factory.registerVuexModule();
        var accessor = function () {
            return factory.buildAccessor();
        };
        accessor.prototype = constructor.prototype;
        return accessor;
    };
}
